@import '../../assets/styles/variables';
.header-container {   
    height: 100px;
    color: #FDFFFF;
    background-color: $dark-gray;
    position: sticky;
    top: 0px;
    display: flex;
    align-items: center;
    display: flex;
    z-index: 20;
    padding-left: 200px;
    padding-right: 200px;
    @media screen and (max-width: 1520px) { 
        padding-left: 20px;
        padding-right: 20px;
    }
}
.header {   
/*     height: 100px;
    color: #FDFFFF;
    background-color: $dark-gray;
    position: sticky; */
    display: flex;
    align-items: center;
    display: flex;
    z-index: 20;
    width: 100%;
    justify-content: space-between;
}

.logo {
    font-size: 35px;
/*     padding-left: 200px; */
    .nombre {
        font-weight: 500;
    }
}

.desktop-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @media screen and (max-width: 1400px) {
        display: none;
    }
}

.desktop-mobile {
    @media screen and (min-width: 1400px) { 
        display: none;
    }
}





/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: $white;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #a90000;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: $white !important;
    height: 30px !important;
    height: 30px !important;
/*     margin-right: 30px;
    margin-top: 30px; */
/*     button {
        font-size: 30px;
    } */
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
    top: 0px !important;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #1e1f22;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

  .react-burger-cross-btn {
  font-size: 30px;
  }