
.home-container {
    background-color: #2C2F33;
    /* min-height: calc( 100vh - 100px ); */
    min-height: calc( 100vh );
    width: 100vw;
    display: flex;
    @media only screen and (max-width: 1170px) {
      flex-direction: column;
    }
  }
  
.scroll-down {
  position: absolute;
  bottom: 100px;
  right: 48%;
  width: 60px;
}

@media only screen and (max-width: 460px) {
  .scroll-down {
    display: none;
  }
}