@import '../../assets/styles/variables';

.portfolio {
    background: $black;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-bottom: 200px;
  }


  .pills-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 25px;
  }

  .experience-badge {
    background: #919191 !important;
    color: white;
    padding: 0px 15px;
    border-radius: 25px;
    margin-right: 5px;
    margin-top: 4px
 }


  .text-left {
    width: 100%;
    text-align: left;
  }
  
  .portfolio .list {
    margin: 10px;
    margin-bottom: 45px;
    padding: 0;
    list-style: none;
    display: flex;
    width: 85%;
    justify-content: space-around;
    align-items: center;
    @media screen and (max-width: 1400px) {
         flex-wrap: wrap;
         margin-bottom: 15px;
        div {
          margin-top: 15px;
          
        }
        }
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .column {
    display: flex;
    flex: 25%;
    max-width: 25%;
    width: 100%;
    margin-right: 1.2rem;
    margin-top: 1.2rem;
    align-self: normal !important;
    border-radius: 5px;
/*     box-shadow: 1px 1px 4px gray; */
    color: white;
    transition: all 0.6s ease;
    position: relative;
    align-items: flex-end;
    justify-content: center;
    @media screen and (max-width: 950px) {
      flex-direction: column;
      background-color: rgba(0, 0, 0, 0.623);;
    }
  }
  
  .column img {
    border-radius: 5px;
    vertical-align: middle;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
  }
  
  .overlay {
    position: absolute;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.623);
    padding: 1rem 1.5rem;
    display: flex;
    transition: all 0.5s ease;
    @media screen and (max-width: 950px) {
      position: relative;
      width: auto;
    }
  }
  
  .overlay .left {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 25px;
  }
  
  .overlay h3 {
    font-size: 22px;
    font-weight: 400;
    color: $green;
    padding: 0;
    margin: 0;
  }
  
  .overlay p {
    font-size: medium;
    color: rgb(245, 241, 241);
    padding: 0;
    margin: 0;
  }
  
  .overlay .right {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .right .icon {
    cursor: pointer;
    color: rgba(255, 255, 255, 0.89);
  }
  
  .right .icon:hover {
    color: $green;
    transform: scale(1.05);
  }
  
  /* MEDIA */
  
  @media screen and (max-width: 1400px) {
    .column {
      flex: 100%;
      max-width: 100%;
      margin: 0;
      margin-top: 1rem;
      margin-top: 40px;
    }
  }
  
  @media screen and (min-width: 1400px) {
    .column {
      flex: 46%;
      max-width: 46%;
      transition: all 2s ease;
      overflow: hidden;
    }
  
    .column img {
      z-index: 0;
      opacity: 1.95;
    }
  
    .column:hover img {
      opacity: 0.5;
      z-index: 0;
    }

    .column img {
      transform: scale(1.7) translate(-110px, -32px);
      transition: all 0.5s ease;
    }
  
    .column:hover img {
      transform: scale(1.7) translate(+140px, -32px);
      transition: all 0.5s ease;
    }

/*     .column:nth-child(odd) img {
      transform: scale(1) translate(0px, 0px);
      transition: all 0.5s ease;
    }
  
    .column:nth-child(odd):hover img {
      transform: scale(1.7) translate(-110px, -32px);
      transition: all 0.5s ease;
    } */

/*     .column:nth-child(even) img {
      transform: scale(1.7) translate(-110px, -32px);
      transition: all 0.5s ease;
    }
  
    .column:nth-child(even):hover img {
      transform: scale(1) translate(0px, 0px);
      transition: all 0.5s ease;
    } */
  
    .overlay {
      position: absolute;
      padding: 1rem 1.5rem;
      transition: all 0.5s ease;
      bottom: 500px;
      
    }
  
    .column:hover .overlay {
      bottom: 0px;
    }
  
    .portfolio .list {
      width: 50%;
      justify-content: space-evenly;
    }
    
  }